// Root CSS variables

:root,
[data-bs-theme="light"] {
  --#{$prefix}border-radius-xs: #{$border-radius-xs};
  --#{$prefix}border-radius-xxxl: #{$border-radius-xxxl};

  --#{$prefix}font-weight-light: #{$font-weight-light};
  --#{$prefix}font-weight-normal: #{$font-weight-normal};
  --#{$prefix}font-weight-medium: #{$font-weight-medium};
  --#{$prefix}font-weight-semibold: #{$font-weight-semibold};
  --#{$prefix}font-weight-bold: #{$font-weight-bold};

  --#{$prefix}font-size-xs: #{$font-size-xs};
  --#{$prefix}font-size-sm: #{$font-size-sm};
  --#{$prefix}font-size-base: #{$font-size-base};
  --#{$prefix}font-size-lg: #{$font-size-lg};
  --#{$prefix}font-size-xl: #{$font-size-xl};
  --#{$prefix}h1-font-size: #{$h1-font-size};
  --#{$prefix}h2-font-size: #{$h2-font-size};
  --#{$prefix}h3-font-size: #{$h3-font-size};
  --#{$prefix}h4-font-size: #{$h4-font-size};
  --#{$prefix}h5-font-size: #{$h5-font-size};
  --#{$prefix}h6-font-size: #{$h6-font-size};

  --#{$prefix}spacer: #{$spacer};
  --#{$prefix}spacing-0: 0;
  --#{$prefix}spacing-1: #{0.25 * $spacer};
  --#{$prefix}spacing-2: #{0.5 * $spacer};
  --#{$prefix}spacing-3: #{$spacer};
  --#{$prefix}spacing-4: #{1.5 * $spacer};
  --#{$prefix}spacing-5: #{3 * $spacer};

  --#{$prefix}component-color: #{$component-color};
  --#{$prefix}component-hover-color: #{$component-hover-color};
  --#{$prefix}component-hover-bg: #{$component-hover-bg};
  --#{$prefix}component-active-color: #{$component-active-color};
  --#{$prefix}component-active-bg: #{$component-active-bg};
  --#{$prefix}component-disabled-color: #{$component-disabled-color};

  --#{$prefix}user-selection-color: #{$user-selection-color};

  --#{$prefix}box-shadow-xl: #{$box-shadow-xl};

  --plyr-color-main: #{$primary};

  --#{$prefix}line-height-base: #{$line-height-base};

  --#{$prefix}heading-line-height: #{$headings-line-height};
  --#{$prefix}heading-font-weight: #{$headings-font-weight};

  --#{$prefix}display-1: #{map-get($display-font-sizes, 1)};
  --#{$prefix}display-2: #{map-get($display-font-sizes, 2)};
  --#{$prefix}display-3: #{map-get($display-font-sizes, 3)};
  --#{$prefix}display-4: #{map-get($display-font-sizes, 4)};
  --#{$prefix}display-5: #{map-get($display-font-sizes, 5)};
  --#{$prefix}display-6: #{map-get($display-font-sizes, 6)};
  --#{$prefix}display-line-height: #{$display-line-height};

  --#{$prefix}btn-padding-x: #{$btn-padding-x};
  --#{$prefix}btn-padding-y: #{$btn-padding-y};
  --#{$prefix}btn-font-weight: #{$btn-font-weight};
  --#{$prefix}btn-color: #{$white};
  --#{$prefix}btn-border-color: transparent;

  --#{$prefix}nav-link-font-weight: #{$nav-link-font-weight};

  --#{$prefix}gray-950: #{if(map-has-key($grays, 950), map-get($grays, 950), #0a0a0a)};
}


// Dark mode overrides

@if $enable-dark-mode {
  @include color-mode(dark, true) {
    // твои оверрайды
    --#{$prefix}link-color: var(--#{$prefix}info);
    --#{$prefix}link-color-rgb: var(--#{$prefix}info-rgb);
    --#{$prefix}link-hover-color: var(--#{$prefix}info);
    --#{$prefix}link-hover-color-rgb: var(--#{$prefix}info-rgb);

    --#{$prefix}component-color: #{$component-color-dark};
    --#{$prefix}component-hover-color: #{$component-hover-color-dark};
    --#{$prefix}component-hover-bg: #{$component-hover-bg-dark};
    --#{$prefix}component-active-color: #{$component-active-color-dark};
    --#{$prefix}component-active-bg: #{$component-active-bg-dark};
    --#{$prefix}component-disabled-color: #{$component-disabled-color-dark};

    --#{$prefix}user-selection-color-dark: #{$user-selection-color-dark};

    --#{$prefix}focus-ring-opacity: #{$focus-ring-opacity-dark};
    --#{$prefix}focus-ring-color: #{$focus-ring-color-dark};

    --#{$prefix}box-shadow: #{$box-shadow-dark};
    --#{$prefix}box-shadow-sm: #{$box-shadow-sm-dark};
    --#{$prefix}box-shadow-lg: #{$box-shadow-lg-dark};

    /* ДОП. ПЕРЕМЕННЫЕ ДЛЯ theme.json (при необходимости) */
    --#{$prefix}heading-line-height: #{$headings-line-height};
    --#{$prefix}heading-font-weight: #{$headings-font-weight};
    --#{$prefix}display-line-height: #{$display-line-height};

    --#{$prefix}btn-padding-x: #{$btn-padding-x};
    --#{$prefix}btn-padding-y: #{$btn-padding-y};
    --#{$prefix}btn-font-weight: #{$btn-font-weight};
    --#{$prefix}btn-color: #{if(global-variable-exists(btn-color-dark), $btn-color-dark, $btn-color)};
    --#{$prefix}btn-border-color: transparent;

    --#{$prefix}nav-link-font-weight: #{$nav-link-font-weight};

  }
}
