// Utilities
// Extend / update default Bootstrap's utility classes

@import "../../../node_modules/bootstrap/scss/functions";
@import "../../../node_modules/bootstrap/scss/variables";
@import "../../../node_modules/bootstrap/scss/variables-dark";
@import "../../../node_modules/bootstrap/scss/maps";
@import "../../../node_modules/bootstrap/scss/mixins";
@import "../../../node_modules/bootstrap/scss/utilities";

$all-gray-colors: map-merge-multiple(map-prefix-keys($grays, 'gray-'));

/* stylelint-disable @stylistic/value-list-max-empty-lines, @stylistic/function-max-empty-lines */
$utilities: map-merge(
		$utilities,
		(
			"rounded": (
				property: border-radius,
				class: rounded,
				values: (
					null: var(--#{$prefix}border-radius),
					0: 0,
					1: var(--#{$prefix}border-radius-xs),
					2: var(--#{$prefix}border-radius-sm),
					3: var(--#{$prefix}border-radius),
					4: var(--#{$prefix}border-radius-lg),
					5: var(--#{$prefix}border-radius-xl),
					6: var(--#{$prefix}border-radius-xxl),
					7: var(--#{$prefix}border-radius-xxxl),
					circle: 50%,
					pill: var(--#{$prefix}border-radius-pill)
				)
			),
			"rounded-top": (
				property: border-top-left-radius border-top-right-radius,
				class: rounded-top,
				values: (
					null: var(--#{$prefix}border-radius),
					0: 0,
					1: var(--#{$prefix}border-radius-xs),
					2: var(--#{$prefix}border-radius-sm),
					3: var(--#{$prefix}border-radius),
					4: var(--#{$prefix}border-radius-lg),
					5: var(--#{$prefix}border-radius-xl),
					6: var(--#{$prefix}border-radius-xxl),
					7: var(--#{$prefix}border-radius-xxxl),
					circle: 50%,
					pill: var(--#{$prefix}border-radius-pill)
				)
			),
			"rounded-end": (
				property: border-top-right-radius border-bottom-right-radius,
				class: rounded-end,
				values: (
					null: var(--#{$prefix}border-radius),
					0: 0,
					1: var(--#{$prefix}border-radius-xs),
					2: var(--#{$prefix}border-radius-sm),
					3: var(--#{$prefix}border-radius),
					4: var(--#{$prefix}border-radius-lg),
					5: var(--#{$prefix}border-radius-xl),
					6: var(--#{$prefix}border-radius-xxl),
					7: var(--#{$prefix}border-radius-xxxl),
					circle: 50%,
					pill: var(--#{$prefix}border-radius-pill)
				)
			),
			"rounded-bottom": (
				property: border-bottom-right-radius border-bottom-left-radius,
				class: rounded-bottom,
				values: (
					null: var(--#{$prefix}border-radius),
					0: 0,
					1: var(--#{$prefix}border-radius-xs),
					2: var(--#{$prefix}border-radius-sm),
					3: var(--#{$prefix}border-radius),
					4: var(--#{$prefix}border-radius-lg),
					5: var(--#{$prefix}border-radius-xl),
					6: var(--#{$prefix}border-radius-xxl),
					7: var(--#{$prefix}border-radius-xxxl),
					circle: 50%,
					pill: var(--#{$prefix}border-radius-pill)
				)
			),
			"rounded-start": (
				property: border-bottom-left-radius border-top-left-radius,
				class: rounded-start,
				values: (
					null: var(--#{$prefix}border-radius),
					0: 0,
					1: var(--#{$prefix}border-radius-xs),
					2: var(--#{$prefix}border-radius-sm),
					3: var(--#{$prefix}border-radius),
					4: var(--#{$prefix}border-radius-lg),
					5: var(--#{$prefix}border-radius-xl),
					6: var(--#{$prefix}border-radius-xxl),
					7: var(--#{$prefix}border-radius-xxxl),
					circle: 50%,
					pill: var(--#{$prefix}border-radius-pill)
				)
			),
		  // Min width
			"min-width": (
				property: min-width,
				class: min-w,
				values: (
					0: 0,
					100: 100
				)
			),
			"width": (
				property: width,
				class: w,
				values: (
					5: 5%,
					10: 10%,
					15: 15%,
					20: 20%,
					25: 25%,
					30: 30%,
					35: 35%,
					40: 40%,
					45: 45%,
					50: 50%,
					55: 55%,
					60: 60%,
					65: 65%,
					70: 70%,
					75: 75%,
					80: 80%,
					85: 85%,
					90: 90%,
					95: 95%,
					100: 100%
				)
			),
		  // Line height
			"line-height": (
				property: line-height,
				class: lh,
				values: (
					0: 0,
					1: 1,
					sm: $line-height-sm,
					base: $line-height-base,
					lg: $line-height-lg,
				)
			),
		  // Opacity
			"opacity": (
				property: opacity,
				values: (
					8: .08,
					10: .1,
					15: .15,
					25: .25,
					35: .35,
					40: .4,
					50: .5,
					60: .6,
					70: .7,
					75: .75,
					80: .8,
					90: .9,
					100: 1
				)
			),
		  // z-index
			"z-index": (
				property: z-index,
				class: z,
				values: map-merge(
						$zindex-levels,
						(
							sticky: $zindex-sticky,
							fixed: $zindex-fixed
						)
				)
			),
		  // cursor
			"cursor": (
				property: cursor,
				class: cursor,
				values: (
					pointer: pointer,
					default: default,
					help: help,
					wait: wait,
					crosshair: crosshair,
					not-allowed: not-allowed,
					zoom-in: zoom-in,
					zoom-out: zoom-out,
					grab: grab
				)
			),
		  // text-align
			"text-align": map-merge(
					map-get($utilities, "text-align"),
					(
						values: map-merge(
								map-get(map-get($utilities, "text-align"), "values"),
								(justify: justify),
						),
					),
			),
		  // color
			"color": map-merge(
					map-get($utilities, "color"),
					(
						values: map-merge(
								map-get(map-get($utilities, "color"), "values"),
									(
										$all-gray-colors
									),
						),
					),
			),
		  // background-color
			"background-color": map-merge(
					map-get($utilities, "background-color"),
					(
						values: map-merge(
								map-get(map-get($utilities, "background-color"), "values"),
									(
										$all-gray-colors
									),
						),
					),
			),
		  // shadow
			"shadow": map-merge(
					map-get($utilities, "shadow"),
					(
						values: map-merge(
								map-get(map-get($utilities, "shadow"), "values"),
								(
									xl: var(--#{$prefix}box-shadow-xl)
								),
						),
					),
			),
		)
);

// Dotted background
.bg-dotted {
  --#{$prefix}bg-color: var(--#{$prefix}tertiary-bg);
  --#{$prefix}dot-color: var(--#{$prefix}secondary);
  --#{$prefix}dot-size: 1px;
  --#{$prefix}dot-space: 20px;

  background: linear-gradient(90deg, var(--#{$prefix}bg-color) calc(var(--#{$prefix}dot-space) - var(--#{$prefix}dot-size)), transparent 1%) center, linear-gradient(var(--#{$prefix}bg-color) calc(var(--#{$prefix}dot-space) - var(--#{$prefix}dot-size)), transparent 1%) center, var(--#{$prefix}dot-color);
  background-size: var(--#{$prefix}dot-space) var(--#{$prefix}dot-space);
}


// Border styles
.border-dashed {
  --#{$prefix}border-style: dashed;
}

.border-dotted {
  --#{$prefix}border-style: dotted;
}

// Disable transition on element
.transition-none {
  transition: none !important;
}

// Disable transform on element
.transform-none {
  transform: none !important;
}


// Disable background image on element

.bg-image-none {
  background-image: none !important;
}


.bi {
  &.bi-bold {
	-webkit-text-stroke: 1px;
  }
}

.fill-primary {
  fill: $primary !important;
}

.fill-white {
  fill: $white !important;
}

.fill-secondary {
  fill: $secondary !important;
}

.fill-success {
  fill: $success !important;
}

.fill-info {
  fill: $info !important;
}

.fill-warning {
  fill: $warning !important;
}

.fill-danger {
  fill: $danger !important;
}

.fill-light {
  fill: $light !important;
}

.fill-dark {
  fill: $dark !important;
}

.fill-body {
  fill: $body-bg !important;
}

.fill-mode {
  fill: $gray-900 !important;
}

$all-colors: map-merge(
		map-prefix-keys($theme-colors, ''),
		map-merge-multiple(map-prefix-keys($grays, 'gray-'))
);

$blur-levels: (
	sm: 2px,
	md: 5px,
	lg: 10px
);

@each $name, $color in $all-colors {
  @each $blur-name, $blur-value in $blur-levels {
	.backdrop-bg-blur-#{$name}-#{$blur-name} {
	  backdrop-filter: blur(#{$blur-value});
	  -webkit-backdrop-filter: blur(#{$blur-value});
	  background-color: rgba($color, 0.75) !important;
	}
  }
}
